@media (max-width: 590px) {
  .menu-btn {
    display: block;
    position: absolute;
    top: 49px;
    right: 35px;
    width: 30px;
    height: 2px;
    background-color: #fff;
    border: none; }
    .menu-btn::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #fff;
      margin-top: -7px;
      transition: transform 0.15s; }
    .menu-btn::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #fff;
      margin-top: 7px;
      transition: transform 0.15s; }
  .menu-btn-active {
    height: 0px; }
    .menu-btn-active::before {
